  width: 46px;
  margin-right: 0;
}

.copyright{
  margin-bottom: 8px;
}

.privacy{
  margin-bottom: 8px;
}

.footer-legal-links{
  display: block;
}

.terms-privacy{
  margin-left: 0;
}
}

